import React, { Component } from 'react';

import { BitcoinService } from '../../services/BitcoinService';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { loadUser } from '../../store/actions/userActions';
import { eventBus } from '../../services/eventBusService';
import Converter from '../../components/Converter';

import './Homepage.scss';
import bitcoin from '../../assets/imgs/bitcoin.png';
import coins from '../../assets/imgs/coins.png';

class _HomePage extends Component {
  state = {
    rate: '',
  };
  async componentDidMount() {
    this.getUser();
    const rate = await BitcoinService.getRate();
    this.setState({ rate });
  }

  async getUser() {
    const user = await this.props.loadUser();
    if (!user) {
      console.log('no user!');
      this.props.history.push('/signup');
      return;
    }
    eventBus.emit('user loggedIn', user);
  }
  render() {
    const { rate } = this.state;
    const { user } = this.props;
    return (
      <div className="home-page container flex column">
        {user && <h1>Hello {user.name}!</h1>}
        <p className="instructions">
          Go to contacts page to send some bitcoins to your buddies. <br />
          Or, go to your profile page to see past transfers.
          <br />
          Or, head over to the statistics page to see latest up to date data on
          bitcoin.
          <br />
          Spend wisely!
        </p>
        <div className="main flex column align-center">
          <div className="flex auto-center">
            <img src={bitcoin} alt="" />
            {user && <h2> Your Coins: {user.coins}</h2>}
          </div>
          <div className="flex auto-center">
            <img src={coins} alt="" />

            <h2>Rate: 1Bt = {(1 / rate).toFixed(2)} USD </h2>
          </div>
        </div>
        <Converter></Converter>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    user: state.userReducer.user,
  };
};

const mapDispatchToProps = {
  loadUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(_HomePage);
