import React, { Component } from 'react';
import { BitcoinService } from '../../services/BitcoinService';
import Chart from '../../components/Chart/Chart';

class Statistic extends Component {
  state = {
    marketPrice: null,
    confirmedTransactions: null,
    tradeVolume: null,
  };
  componentDidMount() {
    this.getMarketPrice();
    this.getConfirmedTransactions();
    this.getTradeVolume();
  }

  async getMarketPrice() {
    const marketPrice = await BitcoinService.getMarketPrice();

    this.setState({ marketPrice });
  }
  async getConfirmedTransactions() {
    const confirmedTransactions = await BitcoinService.getConfirmedTransactions();
    this.setState({ confirmedTransactions });
  }
  async getTradeVolume() {
    const tradeVolume = await BitcoinService.getTradeVolume();
    this.setState({ tradeVolume });
  }
  render() {
    const { marketPrice } = this.state;
    const { confirmedTransactions } = this.state;
    const { tradeVolume } = this.state;

    return (
      <section>
        <div className="statistic-page container">
          {marketPrice && <Chart data={marketPrice} color="#004188b3" />}
        </div>
        <div className="statistic-page container">
          {confirmedTransactions && (
            <Chart data={confirmedTransactions} color="#ff443db3" />
          )}
        </div>
        <div className="statistic-page container">
          {tradeVolume && <Chart data={tradeVolume} color="#ff3dc2b3" />}
        </div>
      </section>
    );
  }
}

export default Statistic;
