import './Header.scss';
import { Component } from 'react';
import { NavLink, Link, withRouter } from 'react-router-dom';
import { eventBus } from '../../services/eventBusService';
import home from '../../assets/imgs/home.png';
import users from '../../assets/imgs/users.png';
import graph from '../../assets/imgs/graph.png';
import profile from '../../assets/imgs/profile.png';

class _Header extends Component {
  componentDidMount() {
    console.log(this.props);
    eventBus.on('details mounted', () => {
      console.log('Details are now mounted');
    });
  }
  // onBack = () => {
  //   this.props.history.goBack();
  // };
  render() {
    return (
      <nav className="header flex auto-center">
        <Link to="/">
          <img src={home} alt="home" title="home" />
        </Link>
        <Link to="/contacts">
          <img src={users} alt="contacts" title="contacts" />
        </Link>
        <Link to="/statistic">
          <img src={graph} alt="statistics" title="statistics" />
        </Link>
        <Link to="/user-profile">
          <img src={profile} alt="profile" title="profile" />
        </Link>
      </nav>
    );
  }
}

export const Header = withRouter(_Header);
