import axios from 'axios';
export const BitcoinService = {
  getRate,
  getMarketPrice,
  getConfirmedTransactions,
  getTradeVolume,
};

async function getRate(amount = 1, curr = 'USD') {
  const rate = await axios.get(
    `https://blockchain.info/tobtc?currency=${curr}&value=${amount}&cors=true`
  );
  // console.log(
  //   "🚀 ~ file: BitcoinService.js ~ line 12 ~ getRate ~ rate",
  //   rate.data
  // );
  return rate.data;
}
async function getMarketPrice() {
  const marketPrice = await axios.get(
    `https://api.blockchain.info/charts/market-price?timespan=5months&format=json&cors=true`
  );

  return marketPrice.data;
}
async function getConfirmedTransactions() {
  const res = await axios.get(
    'https://api.blockchain.info/charts/avg-block-size?timespan=1months&format=json&cors=true'
  );
  return res.data;
}

async function getTradeVolume() {
  const res = await axios.get(
    'https://api.blockchain.info/charts/trade-volume?timespan=1months&format=json&cors=true'
  );
  return res.data;
}
